import React,{useState} from 'react'
import './components/app.css'
import LeftChat from './components/Message/LeftChat'
import RightChat from './components/Message/RightChat'


const Chats = () => {
    const [message, setMessage] = useState('')
    const [messageData, setMessageData] = useState([])

    const sendMessage = () => {
        messageData.push(message)
        setMessageData([...messageData])
    }

    return (
        <div className="wrapper">
            <div className="header">
                <div className="avatar"></div>
                <div className="username_container">
                    <div className="username">Kehinde Ayodele</div>
                    <div className="online_status">Online</div>
                </div>
            </div>
            <div className="chat_container">
                {messageData.length > 0 && messageData.map((message) => {
                    return <RightChat message={message} />
                })}
                
            </div>
            <div className="chat_footer">
                <div className="form_container">
                    <input type="text" onChange={(e) => setMessage(e.target.value)} className="form_input" placeholder="Write here..." />
                </div>
                <div className="send_button" onClick={() => sendMessage()}></div>
            </div>
        </div>
    )
}


export default Chats